@import url('https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap');
 
body {
   font-family: 'Roboto', sans-serif;
   display: flex;
   justify-content: center;
   align-items: center;
   min-height: 100vh;
}
 ul {
   list-style: none;
   display: flex;
}
 ul .key {
   position: relative;
   width: 60px;
   height: 180px;
   border: 1px solid black;
   border-right: none;
   background: #fffff0;
   border-radius: 5px;
   box-shadow: 0px 3px 5px #666;
   cursor: pointer;
   display: flex;
   justify-content: center;
   align-items: flex-end;
   padding-bottom: 10px;
   font-weight: bold;
}
 ul .key:last-child {
   border-right: 1px solid black;
}
 ul .black-key {
   position: absolute;
   top: -1px;
   left: 37.5px;
   width: 45px;
   height: 120px;
   background: black;
   border-radius: 5px;
   box-shadow: 0px 3px 5px #666;
   z-index: 999;
   display: flex;
   justify-content: center;
   align-items: flex-end;
   padding-bottom: 10px;
   color: white;
}
 ul .active {
   box-shadow: 0px 1px 3px #666;
}